webpackJsonp([201811504738303],{

/***/ 418:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Yangkui's Blog","author":"Yang Kui"}},"markdownRemark":{"id":"/Users/yk/Documents/blog/src/pages/vscode/index.md absPath of file >>> MarkdownRemark","html":"<p>这篇文章主要是针对打算学习前端的小白，和想学点前端的后端大佬，为了提升你们的编码幸福感。</p>\n<blockquote>\n<p>工欲善其功，必先利其器</p>\n</blockquote>\n<p>简单介绍下背景，VSCode 是目前前端界最为流行的一款<strong>编辑器</strong>，每月一次的更新，强大的插件生态和 TypeScript 的整合，让这款编辑器瞬间风靡世界。像之前的 Sublime 和 GitHub 的 Atom，都无法与它抗衡。比如 <a href=\"https://vuejs.org\">Vue</a> 的作者 Evan You 也是用它作为主力开发工具。</p>\n<h2>准备工作</h2>\n<p>第一步当然是下载 <a href=\"https://code.visualstudio.com/\">VSCode</a> 并安装。Window 用户印象中还需要下载一个 Git 的工具，无论你知不知道的 Git，以及 Git 和 GitHub 的区别，反正去<a href=\"https://git-scm.com/\">官网</a> 下载好这个并安装。如果你在终端（命令行）中输入</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token function\">git</span> --version</code></pre>\n      </div>\n<p>会有一个版本号输出的话，就说明下载成功了。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-0648f1f5a16dfb03dd0a812f5e030e05-d3c11.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 7.00218818380744%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAABCAYAAADeko4lAAAACXBIWXMAABYlAAAWJQFJUiTwAAAAVklEQVQI1xXGKxKAIABAQQJDkWDzHDYZqA5SxOAYLX6iRcEbeO2npl1hTYMxBuscIQS87xj3xHw9DFumXy7imonb/f33ZEot8agpqwIpJVprlFIIIXgB9H8jk7lhonkAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-0648f1f5a16dfb03dd0a812f5e030e05-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-0648f1f5a16dfb03dd0a812f5e030e05-f931c.png 200w,\n/blogs/static/DraggedImage-0648f1f5a16dfb03dd0a812f5e030e05-e8031.png 400w,\n/blogs/static/DraggedImage-0648f1f5a16dfb03dd0a812f5e030e05-42603.png 800w,\n/blogs/static/DraggedImage-0648f1f5a16dfb03dd0a812f5e030e05-5b8b9.png 1200w,\n/blogs/static/DraggedImage-0648f1f5a16dfb03dd0a812f5e030e05-c7ea5.png 1600w,\n/blogs/static/DraggedImage-0648f1f5a16dfb03dd0a812f5e030e05-d3c11.png 1828w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \nMac 用户只要安装过 Xcode 或者开发者工具套件的话，就内置了 Git 的功能。之所以要安装 git 是因为 VS Code 的插件基于 Git 的支持。</p>\n<p>接着还需要安装，<a href=\"nodejs.org\">node</a>，<a href=\"https://yarnpkg.com/lang/en/\">yarn</a> 英文不好的同学也不要管那么多，看到 <strong>Install, Download</strong> 这种单词就点，下版本最高的就行。Window 用户的 node 需要下载的是一个 <code class=\"language-text\">.msi</code> 文件不是 <code class=\"language-text\">.exe</code> 文件，<code class=\"language-text\">msi</code> 文件能给 Window 环境写入一个二进制文件，而 <code class=\"language-text\">exe</code> 仅仅是一个 node 的 REPL 环境。</p>\n<blockquote>\n<p>因为作者本人对 Window 不够熟悉，很多描述是靠之前碰到过的坑，如果碰到问题的话，还请自己使用搜索引擎解决</p>\n</blockquote>\n<p>还是和之前一样，输入几个命令测试是否安装成功。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">node -v\nyarn why</code></pre>\n      </div>\n<p>在这两个都确定安装好后，继续输入</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">yarn global add eslint</code></pre>\n      </div>\n<p>Window 用户如果 Yarn 没有安装成功的话，就使用 npm 安装作为 B 计划</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token function\">npm</span> <span class=\"token function\">install</span> -g eslint</code></pre>\n      </div>\n<p>安装好后，就会有打印出安装成功的 eslint 版本。<a href=\"http://eslint.cn/\">eslint</a> 是一个 JS 的静态分析器，它可以解决一些写 JS 经常遇到的问题：</p>\n<ol>\n<li>变量名写错了，但是没有报错。</li>\n<li>自己不想写分号，让它自动加</li>\n<li>强制要求格式的规范</li>\n<li>等等</li>\n</ol>\n<p>还有 <a href=\"https://www.typescriptlang.org/\">TypeScript</a> 这是微软开发的一个 JS 超集，安装它的目的主要是它能够为 VS Code 赋予更强的能力，让开发更爽。设置里面会有很多功能性开关，需要安装了指定版本的 typescript 才可以，所以我们安装最新的就是了。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">yarn global add typescript\n// or\n<span class=\"token function\">npm</span> <span class=\"token function\">install</span> -g typescript</code></pre>\n      </div>\n<p>和上面一样，如果 yarn 没安装成功的话就是用 npm。在本文写作的时候，typescript 的版本为 <code class=\"language-text\">2.9.1</code>\n接下来就进入 VSCode 的流程。</p>\n<hr>\n<p>最基本的 VSCode 的侧边栏有五项\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-1-3acbbb2f132d7f0b2efc67f5e1a65f70-c29b3.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 102px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 525.4901960784314%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 1\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-1-3acbbb2f132d7f0b2efc67f5e1a65f70-c29b3.png\"\n        srcset=\"/blogs/static/DraggedImage-1-3acbbb2f132d7f0b2efc67f5e1a65f70-c29b3.png 102w\"\n        sizes=\"(max-width: 102px) 100vw, 102px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n其中第五个就是安装插件的一个 Tab，点开来，选择常用的拓展，就能看到根据安装量排序的拓展工具。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-2-f20afe910439f6f7b19ca45e9cc848c3-2e17d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 99.75550122249389%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 2\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-2-f20afe910439f6f7b19ca45e9cc848c3-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-2-f20afe910439f6f7b19ca45e9cc848c3-f931c.png 200w,\n/blogs/static/DraggedImage-2-f20afe910439f6f7b19ca45e9cc848c3-e8031.png 400w,\n/blogs/static/DraggedImage-2-f20afe910439f6f7b19ca45e9cc848c3-42603.png 800w,\n/blogs/static/DraggedImage-2-f20afe910439f6f7b19ca45e9cc848c3-2e17d.png 818w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n如果真的想要好好运用 VSCode 的话，还是要自己多花费点时间在这些拓展上，找出几个真正适合你的。</p>\n<h2>美化一下</h2>\n<p>默认的 VSCode 还是蛮丑的，特别是底部蓝色的状态栏。显然需要美化一下, 从三个方面着手</p>\n<ol>\n<li>字体</li>\n<li>主题</li>\n<li>文件图标</li>\n</ol>\n<h3>字体</h3>\n<p>选一个好看的字体还是挺重要的，如果每天面对 Window 的 cmd 那种字体的话，写程序一定会十分痛苦。目前广受好评的一个开源字体是，<a href=\"https://github.com/tonsky/FiraCode\">Fira Code</a>, 好看是一方面，还支持一个特别神奇的 <em>font ligatures</em> 功能，它的效果就是能将你输入的一些特定的符号组合，换成另外一个更为<strong>写实风格</strong>的符号。比如我们常用的 <code class=\"language-text\">!=</code> 最后就长得和 <code class=\"language-text\">≠</code> 一样。 \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/fira-code-34775835d427cb3f5ef2b4415bfc6afa-00685.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 97.82135076252723%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"fira code\"\n        title=\"\"\n        src=\"/blogs/static/fira-code-34775835d427cb3f5ef2b4415bfc6afa-42603.png\"\n        srcset=\"/blogs/static/fira-code-34775835d427cb3f5ef2b4415bfc6afa-f931c.png 200w,\n/blogs/static/fira-code-34775835d427cb3f5ef2b4415bfc6afa-e8031.png 400w,\n/blogs/static/fira-code-34775835d427cb3f5ef2b4415bfc6afa-42603.png 800w,\n/blogs/static/fira-code-34775835d427cb3f5ef2b4415bfc6afa-5b8b9.png 1200w,\n/blogs/static/fira-code-34775835d427cb3f5ef2b4415bfc6afa-c7ea5.png 1600w,\n/blogs/static/fira-code-34775835d427cb3f5ef2b4415bfc6afa-00685.png 1836w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>通过 GitHub 下载好字体文件后，安装到系统上。安装好后，进入 VSCode 的设置，设置 <code class=\"language-text\">editor.fontLigatures</code> 为 true，在 <code class=\"language-text\">editor.fontFamily</code> 的最前面加上 <code class=\"language-text\">&#39;Fira Code&#39;</code>，保存一下，去看看效果吧！</p>\n<h3>主题</h3>\n<p>2 和 3 就不需要这些步骤了，通过按住 <code class=\"language-text\">Cmd/Ctrl</code>，接着看 K 和 T 两个键，就可以选择其他主题。不过自带的主题如果不过瘾的话，还可以下载其他主题，推荐安装一个 <em>Dracula</em></p>\n<p>而文件图标的效果就是让你的文件或者文件夹有一个小图标，提供辨识度。像这样 \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/file-icon-d76beb34e8f3330dbe8bb2368c55950a-13b6e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 524px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 82.82442748091601%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"file icon\"\n        title=\"\"\n        src=\"/blogs/static/file-icon-d76beb34e8f3330dbe8bb2368c55950a-13b6e.png\"\n        srcset=\"/blogs/static/file-icon-d76beb34e8f3330dbe8bb2368c55950a-549d5.png 200w,\n/blogs/static/file-icon-d76beb34e8f3330dbe8bb2368c55950a-11c0c.png 400w,\n/blogs/static/file-icon-d76beb34e8f3330dbe8bb2368c55950a-13b6e.png 524w\"\n        sizes=\"(max-width: 524px) 100vw, 524px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n这个功能隐藏的比较深，需要直接去设置。按 F1，输入 <code class=\"language-text\">File Icon Theme</code> 就能找到，官方也提供了几个主题，但是不怎么全，也不是那么好看。本人推荐 <em>Material Icon Theme</em></p>\n<p>有了好看的编辑器，才有动力写出好看的代码。</p>\n<h2>安装插件</h2>\n<p>先看看，需要配置的插件</p>\n<h3>ESLint</h3>\n<p>现在没有 ESLint 已经没法写 JS 代码了，我们不需要花太多时间在找自己的拼写错误导致的 bug 上。下载 VSCode 的 ESLint 插件后，重载 VSCode，打开设置，搜索 <em>eslint</em>。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-3-e9220b82be80fb3c7b82126a19a0f78d-bc0fd.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 26.344086021505376%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 3\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-3-e9220b82be80fb3c7b82126a19a0f78d-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-3-e9220b82be80fb3c7b82126a19a0f78d-f931c.png 200w,\n/blogs/static/DraggedImage-3-e9220b82be80fb3c7b82126a19a0f78d-e8031.png 400w,\n/blogs/static/DraggedImage-3-e9220b82be80fb3c7b82126a19a0f78d-42603.png 800w,\n/blogs/static/DraggedImage-3-e9220b82be80fb3c7b82126a19a0f78d-bc0fd.png 1116w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n这里主要需要修改的几个配置。</p>\n<ul>\n<li>\n<p><code class=\"language-text\">eslint.autoFixOnSave</code></p>\n</li>\n<li>\n<p><code class=\"language-text\">eslint.packageManager</code></p>\n<p>其中第一个设置是可以在每次保存的时候，自动修复一些<strong>能自动修复</strong>的错误，比如少了分号，缩进不对等等… 注意 VSCode 的设置不能直接修改，需要复制到右边的用户设置区，来修改。通过点击左侧的那个 edit 图标就能复制到右侧，接着修改后再保存就能生效。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-4-c145d6992b35b4c76d6b5de65dd7cac3-655e1.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 13.904761904761905%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 4\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-4-c145d6992b35b4c76d6b5de65dd7cac3-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-4-c145d6992b35b4c76d6b5de65dd7cac3-f931c.png 200w,\n/blogs/static/DraggedImage-4-c145d6992b35b4c76d6b5de65dd7cac3-e8031.png 400w,\n/blogs/static/DraggedImage-4-c145d6992b35b4c76d6b5de65dd7cac3-42603.png 800w,\n/blogs/static/DraggedImage-4-c145d6992b35b4c76d6b5de65dd7cac3-655e1.png 1050w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n第二个设置是根据你使用 yarn 还是 npm 安装 eslint 来确定的。如果使用的是 yarn，就改成 yarn；npm 保存不变。</p>\n</li>\n</ul>\n<p>eslint 通过一个<strong>规则文件</strong>驱动，可以叫做 <code class=\"language-text\">.eslintrc, .eslintrc.json, .eslintrc.js</code> 等等，刚开始的时候，你可能不知道 eslint 有哪些规则，这该怎么办？我们可以使用 eslint 默认的设置，来入门。</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">eslint --init</code></pre>\n      </div>\n<p>接着会需要你回答一些和偏好有关的问题</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">How would you like to configure ESLint?\n<span class=\"token operator\">></span> Answer questions about your style\n  Use a popular style guide\n  Inspect your JavaScript file<span class=\"token punctuation\">(</span>s<span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>类似于这样的问题。用方向键控制，回车来确定。最简单的方式是：</p>\n<ol>\n<li>Answer questions about your style</li>\n<li>y</li>\n<li>y</li>\n<li>这个问题是根据你是在浏览器还是在 node 上</li>\n<li>这个问题是问你是否使用 CommonJS，如果不写 node 的话，选择 N</li>\n<li>Do you use JSX? 写 React 的话，就选 y</li>\n<li>Spaces</li>\n<li>Single</li>\n<li>根据电脑系统选择换行的组成。LF/CRLF</li>\n<li>y</li>\n<li>JSON\n根据我这个配置会生成如下配置代码</li>\n</ol>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\"><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">\"env\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">\"browser\"</span><span class=\"token operator\">:</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"es6\"</span><span class=\"token operator\">:</span> <span class=\"token boolean\">true</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"extends\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"eslint:recommended\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"parserOptions\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">\"sourceType\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"module\"</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"rules\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">\"indent\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">[</span><span class=\"token string\">\"error\"</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"linebreak-style\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">[</span><span class=\"token string\">\"error\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"unix\"</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"quotes\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">[</span><span class=\"token string\">\"error\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"single\"</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n    <span class=\"token property\">\"semi\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">[</span><span class=\"token string\">\"error\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"always\"</span><span class=\"token punctuation\">]</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>注意 <code class=\"language-text\">indent</code> 这个 key，根据你的偏好来调整数字。4 表示的是 4 个空格。接着根据这个配置文件，eslint 就能发挥作用了。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-5-56a79be7625a59306c6ee766158c9e6f-27268.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 36.09022556390977%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 5\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-5-56a79be7625a59306c6ee766158c9e6f-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-5-56a79be7625a59306c6ee766158c9e6f-f931c.png 200w,\n/blogs/static/DraggedImage-5-56a79be7625a59306c6ee766158c9e6f-e8031.png 400w,\n/blogs/static/DraggedImage-5-56a79be7625a59306c6ee766158c9e6f-42603.png 800w,\n/blogs/static/DraggedImage-5-56a79be7625a59306c6ee766158c9e6f-27268.png 1064w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n像我这个代码就有很多波浪线出现，就是因为 eslint 在抗议了。第一个错误和第三个错误是因为我们在设置的时候没有选择 <code class=\"language-text\">commonJS</code> 导致的。<code class=\"language-text\">console.log</code> 下面会有波浪线是因为 <code class=\"language-text\">eslint:recommended</code> 的默认配置里，包含了一个选项\n<code class=\"language-text\">no-console: error</code>，意思就是语句中不能出现 <code class=\"language-text\">console</code>，否则就是 error（error 还是 warn 决定了能够成功编译文件，出现了 error 就不行）。调整的方法也很简单，在 <code class=\"language-text\">rules</code> 中添加一句 <code class=\"language-text\">no-console: &#39;off&#39;</code> 来覆盖这个设置就行。</p>\n<h3>Code Runner</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-6-6f4fbf287fa23b7fccd92a108843be15-2dc60.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 35%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 6\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-6-6f4fbf287fa23b7fccd92a108843be15-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-6-6f4fbf287fa23b7fccd92a108843be15-f931c.png 200w,\n/blogs/static/DraggedImage-6-6f4fbf287fa23b7fccd92a108843be15-e8031.png 400w,\n/blogs/static/DraggedImage-6-6f4fbf287fa23b7fccd92a108843be15-42603.png 800w,\n/blogs/static/DraggedImage-6-6f4fbf287fa23b7fccd92a108843be15-5b8b9.png 1200w,\n/blogs/static/DraggedImage-6-6f4fbf287fa23b7fccd92a108843be15-c7ea5.png 1600w,\n/blogs/static/DraggedImage-6-6f4fbf287fa23b7fccd92a108843be15-2dc60.png 2280w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n这个插件的功能是通过调用的系统的二进制文件，来直接运行代码，类似于 IDE 中的编译运行。不过在真正使用之前还需要稍微配置一下，在用户设置中搜索，<code class=\"language-text\">code-runner</code>，可以看到搜索出来的结果。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-7-705799511d2df6c9957faea510f32273-0fd3b.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 67.37481031866464%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 7\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-7-705799511d2df6c9957faea510f32273-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-7-705799511d2df6c9957faea510f32273-f931c.png 200w,\n/blogs/static/DraggedImage-7-705799511d2df6c9957faea510f32273-e8031.png 400w,\n/blogs/static/DraggedImage-7-705799511d2df6c9957faea510f32273-42603.png 800w,\n/blogs/static/DraggedImage-7-705799511d2df6c9957faea510f32273-5b8b9.png 1200w,\n/blogs/static/DraggedImage-7-705799511d2df6c9957faea510f32273-0fd3b.png 1318w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n有很多的配置，可能很让人头大，但是先别慌，找几个我们要用的就行。</p>\n<ul>\n<li><code class=\"language-text\">clearPreviousOutPut</code></li>\n<li><code class=\"language-text\">runInTerminal</code></li>\n<li><code class=\"language-text\">saveFileBeforeRun</code> or <code class=\"language-text\">saveAllFilesBeforeRun</code></li>\n</ul>\n<p>第一个的意思就是每次运行会清楚之前的输入，看个人喜好吧，我是觉得清楚一下会更好。第二个就是在自己系统的终端中运行这个，好处是可以直接打通我们<strong>心爱的终端</strong>，而且如果不配置这个的话，Window 会出现一些奇怪的问题，强烈推荐。</p>\n<p>第三个的意思是，运行之前保存当前文件和运行之前保存所有文件，自己选择一个设置为 True 就行。为什么需要这个？因为我们经常会碰到写完代码就去运行，但是发现自己忘记保存的情况，结果还是之前的结果。JS 的通过 node 来运行的，在我们安装后这个插件并重启后，新建一个 js 文件。比如像我下面是一个生成 HTML 模板的代码,</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">template</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>title<span class=\"token punctuation\">,</span> content<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token template-string\"><span class=\"token string\">`\n  &lt;section>\n\t&lt;h1></span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>title<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">&lt;/h1>\n\t&lt;article></span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>content<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">&lt;/article>\n  &lt;/section>\n`</span></span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> config <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span>\n  <span class=\"token punctuation\">{</span> title<span class=\"token punctuation\">:</span> <span class=\"token string\">'I can run js'</span><span class=\"token punctuation\">,</span> article<span class=\"token punctuation\">:</span> <span class=\"token string\">'Very Cool!'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">{</span> title<span class=\"token punctuation\">:</span> <span class=\"token string\">'I can run everything'</span><span class=\"token punctuation\">,</span> article<span class=\"token punctuation\">:</span> <span class=\"token string\">'Really Cool!'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> theHtml <span class=\"token operator\">=</span> config\n  <span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> title<span class=\"token punctuation\">,</span> content <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token function\">template</span><span class=\"token punctuation\">(</span>title<span class=\"token punctuation\">,</span> content<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">.</span><span class=\"token function\">join</span><span class=\"token punctuation\">(</span><span class=\"token string\">'\\n'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>theHtml<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>之后点击右上角的的这个播放键\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-8-ce984e911a270f3bb175a39451ef4564-d6195.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 274px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 30.656934306569344%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 8\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-8-ce984e911a270f3bb175a39451ef4564-d6195.png\"\n        srcset=\"/blogs/static/DraggedImage-8-ce984e911a270f3bb175a39451ef4564-2649b.png 200w,\n/blogs/static/DraggedImage-8-ce984e911a270f3bb175a39451ef4564-d6195.png 274w\"\n        sizes=\"(max-width: 274px) 100vw, 274px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n就能看到 <code class=\"language-text\">console.log</code> 的结果被打印出来。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-9-701358a41d5df7b22b23a24cc6fc90f2-3debb.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 40.899795501022496%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 9\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-9-701358a41d5df7b22b23a24cc6fc90f2-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-9-701358a41d5df7b22b23a24cc6fc90f2-f931c.png 200w,\n/blogs/static/DraggedImage-9-701358a41d5df7b22b23a24cc6fc90f2-e8031.png 400w,\n/blogs/static/DraggedImage-9-701358a41d5df7b22b23a24cc6fc90f2-42603.png 800w,\n/blogs/static/DraggedImage-9-701358a41d5df7b22b23a24cc6fc90f2-3debb.png 978w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Code Runner 不仅仅支持 JS。我们需要这个功能可能是为了学习新语法，也可能是测试某个算法。显然使用 node REPL 或者在浏览器的 console 里写一大串代码是一个很愚蠢的行为。</p>\n<h3>Prettier</h3>\n<p>这个是最近一年最火的格式化工具，能够将你写成 💩 一样的 JS 也能格式成十分漂亮的代码（前提是没有语法没错）。同时也支持 HTML，CSS，JSON，Markdown 等等文件。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-10-ec5e2d1128d5525805b70ec49d5bd3f4-634a2.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 17.74622892635315%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 10\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-10-ec5e2d1128d5525805b70ec49d5bd3f4-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-10-ec5e2d1128d5525805b70ec49d5bd3f4-f931c.png 200w,\n/blogs/static/DraggedImage-10-ec5e2d1128d5525805b70ec49d5bd3f4-e8031.png 400w,\n/blogs/static/DraggedImage-10-ec5e2d1128d5525805b70ec49d5bd3f4-42603.png 800w,\n/blogs/static/DraggedImage-10-ec5e2d1128d5525805b70ec49d5bd3f4-5b8b9.png 1200w,\n/blogs/static/DraggedImage-10-ec5e2d1128d5525805b70ec49d5bd3f4-c7ea5.png 1600w,\n/blogs/static/DraggedImage-10-ec5e2d1128d5525805b70ec49d5bd3f4-634a2.png 2254w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n为了体现它的强大，我们需要去改一些配置。和之前一样，在设置中搜索 <code class=\"language-text\">format</code>，找到</p>\n<ul>\n<li><code class=\"language-text\">editor.foramtOnPaste</code></li>\n<li><code class=\"language-text\">editor.formatOnSave</code></li>\n<li><code class=\"language-text\">editor.formatOnType</code></li>\n</ul>\n<p>解释已经有中文了，全部设置为 true 就行。至于开启它们的效果，只能自己去好好体会了。<strong>写一点缩进比较烂的代码，接着保存，所有的代码就被格式化了。</strong></p>\n<hr>\n<p>不需要配置的插件</p>\n<h3>Bracket Pair Colorizer</h3>\n<p>如果仅仅看它的介绍的话，这个插件是一个会将你的 <code class=\"language-text\">([{}])</code> 这些使用不同的颜色区分出来，提升鉴别度。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-11-b9913877db7acc2c61cdb0f6bb6ddf86-23728.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 374px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 24.598930481283425%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 11\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-11-b9913877db7acc2c61cdb0f6bb6ddf86-23728.png\"\n        srcset=\"/blogs/static/DraggedImage-11-b9913877db7acc2c61cdb0f6bb6ddf86-80b0e.png 200w,\n/blogs/static/DraggedImage-11-b9913877db7acc2c61cdb0f6bb6ddf86-23728.png 374w\"\n        sizes=\"(max-width: 374px) 100vw, 374px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n可以看到这些括号都是彩色的。当然如果仅仅只是这么一个花哨的功能的话，对编码幸福感提升不大。其实我最看重的是它的<strong>范围高亮功能</strong>，嗯这个名字是我乱取的。就像这样：\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-12-1ce670f817f4157b59c32b142b47fd99-50a2d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 448px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 54.46428571428571%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 12\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-12-1ce670f817f4157b59c32b142b47fd99-50a2d.png\"\n        srcset=\"/blogs/static/DraggedImage-12-1ce670f817f4157b59c32b142b47fd99-fd10f.png 200w,\n/blogs/static/DraggedImage-12-1ce670f817f4157b59c32b142b47fd99-00869.png 400w,\n/blogs/static/DraggedImage-12-1ce670f817f4157b59c32b142b47fd99-50a2d.png 448w\"\n        sizes=\"(max-width: 448px) 100vw, 448px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n注意到了吗，这对 <code class=\"language-text\">[]</code> 之间是一条折线，来表明这个括号所表示的范围。这个功能在较长代码和嵌套的时候，特别有用。</p>\n<h3>Auto Close Tag</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-13-f042e1918db50d4f0fe60e49be37b59f-1274e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 498px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 30.120481927710845%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 13\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-13-f042e1918db50d4f0fe60e49be37b59f-1274e.png\"\n        srcset=\"/blogs/static/DraggedImage-13-f042e1918db50d4f0fe60e49be37b59f-8a03d.png 200w,\n/blogs/static/DraggedImage-13-f042e1918db50d4f0fe60e49be37b59f-61462.png 400w,\n/blogs/static/DraggedImage-13-f042e1918db50d4f0fe60e49be37b59f-1274e.png 498w\"\n        sizes=\"(max-width: 498px) 100vw, 498px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>这个不解释了，就是自动帮你填充对应的 Tag。如果你对 emmet 用的很熟练的话，这个插件可能出场率不高。</p>\n<h3>Auto Rename Tag</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-14-d9451876d2a67a0beb4a8ad48cf93f0a-81941.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 25.718608169440245%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 14\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-14-d9451876d2a67a0beb4a8ad48cf93f0a-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-14-d9451876d2a67a0beb4a8ad48cf93f0a-f931c.png 200w,\n/blogs/static/DraggedImage-14-d9451876d2a67a0beb4a8ad48cf93f0a-e8031.png 400w,\n/blogs/static/DraggedImage-14-d9451876d2a67a0beb4a8ad48cf93f0a-42603.png 800w,\n/blogs/static/DraggedImage-14-d9451876d2a67a0beb4a8ad48cf93f0a-5b8b9.png 1200w,\n/blogs/static/DraggedImage-14-d9451876d2a67a0beb4a8ad48cf93f0a-81941.png 1322w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>和上面一个算是兄弟插件吧。嗯，作者也是同一个人。作用很明显，自动帮你改对应的 tag！同样的，如果你的结构比较复杂的话，这个还是会有点小 bug 的话，不过在时间的节省上，这个插件显然是必装的。emmet 也提供了这个功能，搜索 <code class=\"language-text\">emmet: update tag</code>，这个功能的稳定性应该会高于这个插件，但是要主动去设置快捷键。</p>\n<h3>HTML CSS Support</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-15-bdab489283bc25b9e8067114fcb87cbe-df553.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 494px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 28.74493927125506%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 15\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-15-bdab489283bc25b9e8067114fcb87cbe-df553.png\"\n        srcset=\"/blogs/static/DraggedImage-15-bdab489283bc25b9e8067114fcb87cbe-80f1f.png 200w,\n/blogs/static/DraggedImage-15-bdab489283bc25b9e8067114fcb87cbe-d00a4.png 400w,\n/blogs/static/DraggedImage-15-bdab489283bc25b9e8067114fcb87cbe-df553.png 494w\"\n        sizes=\"(max-width: 494px) 100vw, 494px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>刚写前端的时候，或者为了方便，我们偶尔会把 CSS 写在 HTML 里，但是那样的话是没有直接创建 <code class=\"language-text\">.css</code> 文件强大的自动补全特性的，这个插件让一切都回来了。</p>\n<h3>Settings Sync</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-16-ecd09958faef608f6794cac3bc5e6d9f-df553.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 494px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 27.530364372469634%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 16\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-16-ecd09958faef608f6794cac3bc5e6d9f-df553.png\"\n        srcset=\"/blogs/static/DraggedImage-16-ecd09958faef608f6794cac3bc5e6d9f-80f1f.png 200w,\n/blogs/static/DraggedImage-16-ecd09958faef608f6794cac3bc5e6d9f-d00a4.png 400w,\n/blogs/static/DraggedImage-16-ecd09958faef608f6794cac3bc5e6d9f-df553.png 494w\"\n        sizes=\"(max-width: 494px) 100vw, 494px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>同步你在 VSCode 中的所有设置和下载过的插件，Snippet，快捷键绑定。对于有多台电脑的朋友，这个插件是必备的。不过它是基于 <strong>gist</strong> 来保存你的配置，而这个在国内需要翻墙… 同时这个插件第一次设置的也比较繁琐，需要一定的耐心。总的来说，当你必须换一台电脑的时候，一定要花点时间到这个插件上。可以节省你大量的时间！</p>\n<h3>Path Intellisense</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-17-b1189f788c88c0ba1841df121e97482b-12556.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 492px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 23.17073170731707%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 17\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-17-b1189f788c88c0ba1841df121e97482b-12556.png\"\n        srcset=\"/blogs/static/DraggedImage-17-b1189f788c88c0ba1841df121e97482b-60e82.png 200w,\n/blogs/static/DraggedImage-17-b1189f788c88c0ba1841df121e97482b-2f516.png 400w,\n/blogs/static/DraggedImage-17-b1189f788c88c0ba1841df121e97482b-12556.png 492w\"\n        sizes=\"(max-width: 492px) 100vw, 492px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>提供 URL 的自动补全功能，这个必须装吧。支持任何语言，触发条件一般是在一对引号中，输入 <code class=\"language-text\">./</code> ，<code class=\"language-text\">../</code>, <code class=\"language-text\">/</code> 这些表示路径的前缀，就会跳出来了。像我们的 JS，HTML 中引用文件，CSS 中 <code class=\"language-text\">url</code> 就再也不用担心了。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-18-6c313d49f82f3cd880af2fb5d5804df8-e4e5b.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 77.44186046511628%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 18\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-18-6c313d49f82f3cd880af2fb5d5804df8-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-18-6c313d49f82f3cd880af2fb5d5804df8-f931c.png 200w,\n/blogs/static/DraggedImage-18-6c313d49f82f3cd880af2fb5d5804df8-e8031.png 400w,\n/blogs/static/DraggedImage-18-6c313d49f82f3cd880af2fb5d5804df8-42603.png 800w,\n/blogs/static/DraggedImage-18-6c313d49f82f3cd880af2fb5d5804df8-e4e5b.png 860w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>CSS Peak</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-19-0b0cbd23a4236f22f6570d41b569f6cc-12c38.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 16.063548102383056%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 19\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-19-0b0cbd23a4236f22f6570d41b569f6cc-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-19-0b0cbd23a4236f22f6570d41b569f6cc-f931c.png 200w,\n/blogs/static/DraggedImage-19-0b0cbd23a4236f22f6570d41b569f6cc-e8031.png 400w,\n/blogs/static/DraggedImage-19-0b0cbd23a4236f22f6570d41b569f6cc-42603.png 800w,\n/blogs/static/DraggedImage-19-0b0cbd23a4236f22f6570d41b569f6cc-5b8b9.png 1200w,\n/blogs/static/DraggedImage-19-0b0cbd23a4236f22f6570d41b569f6cc-c7ea5.png 1600w,\n/blogs/static/DraggedImage-19-0b0cbd23a4236f22f6570d41b569f6cc-12c38.png 2266w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>这插件的功能就是在我们看到 HTML 或者 JSX 等代码中，如果出现了 <code class=\"language-text\">class/className</code> 的话，可以通过 <code class=\"language-text\">Cmd/Ctrl</code> ➕ 左键点击某个 class 直接跳转到对应 CSS 的定义。非常方便，但是<strong>并不是每个时候都有用</strong></p>\n<h3>Live Server</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-20-6c73592240d9ad5c9ac39239dca439c8-12c38.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 18.00529567519859%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 20\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-20-6c73592240d9ad5c9ac39239dca439c8-42603.png\"\n        srcset=\"/blogs/static/DraggedImage-20-6c73592240d9ad5c9ac39239dca439c8-f931c.png 200w,\n/blogs/static/DraggedImage-20-6c73592240d9ad5c9ac39239dca439c8-e8031.png 400w,\n/blogs/static/DraggedImage-20-6c73592240d9ad5c9ac39239dca439c8-42603.png 800w,\n/blogs/static/DraggedImage-20-6c73592240d9ad5c9ac39239dca439c8-5b8b9.png 1200w,\n/blogs/static/DraggedImage-20-6c73592240d9ad5c9ac39239dca439c8-c7ea5.png 1600w,\n/blogs/static/DraggedImage-20-6c73592240d9ad5c9ac39239dca439c8-12c38.png 2266w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>这是一个神器，如果你还不会使用 <code class=\"language-text\">webpack 或者 parcel</code> 的话。它提供了自动保存文件后自动刷新的功能。虽然这个功能看上去并不是很起眼，和 <code class=\"language-text\">browser-sync</code> 似乎也差不多，但它能内置到 VSCode 中就大大简化了操作。</p>\n<p>对于刚写前端的朋友有一个很重要的问题，他们往往喜欢直接通过浏览器打开某个 HTML 文件，在浏览器顶部会显示这个 URL 是 <strong>file</strong> 开头的。file 也算是一个协议，和我们知道的 HTTP 协议一样。如果直接通过 file 来打开 HTML 的话，很多 JS 的功能是不能实现的。比如：</p>\n<ol>\n<li>Web Worker</li>\n<li>Service Worker</li>\n<li><code class=\"language-text\">&lt;script type=&quot;module&quot; src=&quot;&quot;&gt;&lt;/script&gt;</code></li>\n<li>cors</li>\n</ol>\n<p>通过使用这个插件，就再也不用担心这个问题。使用方式也很简单，安装好后，点击需要的启动的 HTML 文件中底部 tab 的</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-21-c755ad342baefc7ad5f4e6d46b13e721-642f1.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 200px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 30%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 21\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-21-c755ad342baefc7ad5f4e6d46b13e721-642f1.png\"\n        srcset=\"/blogs/static/DraggedImage-21-c755ad342baefc7ad5f4e6d46b13e721-642f1.png 200w\"\n        sizes=\"(max-width: 200px) 100vw, 200px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>在短暂的等待后，就会自动打开你的默认浏览器跳入由该插件启动的服务器。</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blogs/static/DraggedImage-22-540d5a716800cdbfda68f156e6800d98-b26d0.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 434px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 12.903225806451612%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAYAAACTWi8uAAAACXBIWXMAABYlAAAWJQFJUiTwAAAAjElEQVQI1z2PbQqGIBCEu/+JejuAv4IOkGHhAULyI02dl1ko4WF31mWYHZ7nwXVdcM4hxiiklJBzxn3fUkkp5aP3jtaa9LVW0IOabzjPE9P0wzzPUEphHEcsywJjjKC1xrqu2Pcd27aJPo4D1lqpr2YIMaQ7TZmMKQkTMx1nIQTBe//1/KemCTX33mv+zFPjHMAoO0QAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"DraggedImage 22\"\n        title=\"\"\n        src=\"/blogs/static/DraggedImage-22-540d5a716800cdbfda68f156e6800d98-b26d0.png\"\n        srcset=\"/blogs/static/DraggedImage-22-540d5a716800cdbfda68f156e6800d98-c8443.png 200w,\n/blogs/static/DraggedImage-22-540d5a716800cdbfda68f156e6800d98-e096b.png 400w,\n/blogs/static/DraggedImage-22-540d5a716800cdbfda68f156e6800d98-b26d0.png 434w\"\n        sizes=\"(max-width: 434px) 100vw, 434px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>下次要进去的话，可以直接通过 <code class=\"language-text\">localhost:5500</code>进去。值得一提的是，这个插件仅仅只是一个很方便的功能，但不代表它很强大。需要更强大的开发流程还是需要考虑 parcel 或者 webpack。</p>\n<h2>更多</h2>\n<p>如果还想继续提升开发的幸福感，可以考虑学习</p>\n<ol>\n<li><a href=\"https://emmet.io\">Emmet</a></li>\n<li><a href=\"https://code.visualstudio.com/docs/editor/codebasics\">VSCode 文档</a></li>\n</ol>\n<p>谢谢阅读 🌚</p>","frontmatter":{"title":"使用 VSCode 搭建一个很爽的前端环境","date":"2018/06/04"}}},"pathContext":{"slug":"/vscode/"}}

/***/ })

});


